<template>
  <div id="app">
    <div id="map"></div>
    <navi></navi>
    <search></search>
    <geo></geo>
    <Nuomi></Nuomi>
  </div>
</template>

<style>
#app #map{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: 0;
}
</style>

<script>
import Navi from './components/navi-button.vue'
import Search from './components/search-box.vue'
import Geo from './components/geolocation.vue'
import Nuomi from './components/nuomi-frame.vue'

export default {
  components: {
    Navi,
    Search,
    Geo,
    Nuomi
  },
  ready() {
    // API
    window.map = new BMap.Map('map') // Map
    map.centerAndZoom(new BMap.Point(116.404, 39.915), 11) // ,
    map.setCurrentCity('') //  

    setInterval(() => {
      let arr = document.getElementsByClassName('anchorBL')
      for (let item of arr) {
        item.style['z-index'] = 9
      }
    }, 2000)

    this.initControl()
  },
  methods: {
    initControl() {
      map.addControl(new BMap.NavigationControl({
        anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
        offset: new BMap.Size(20, 200),
        type: BMAP_NAVIGATION_CONTROL_ZOOM
      }))
    }
  }
}
</script>



